.content {
  width: 100%;
  height: 100%;
  border-radius: 15px;
  box-sizing: border-box;
  display: grid;
  justify-content: space-between;
}

.header {
  border-radius: 10px;
  width: 100%;
  background: white;
  height: max-content;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1vmin;
  box-sizing: border-box;
  margin-bottom: 10px;
  flex-wrap: wrap;
  font-size: 0.7em;
  font-weight: bold;

  .category_area {
    width: max-content;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .category_area button {
    margin: 0.5vmin 0.8vmin;
    padding: 5px 8px;
    border-radius: 5px;
    border: none;
    font-size: 1.4em;

  }

  .find {
    margin: 5px;
    font-size: 1.4em;
    width: 30%;
    padding: 3px 6px;

  }

  .active {
    background: navajowhite;
  }
}

